import React, { useState } from 'react'
import './custom.css'
import 'bootstrap/scss/bootstrap.scss'



const AddUserForm = props => {
    const initialFormState = { id: null, name: '', username: '', country: '' }
    const [user, setUser] = useState(initialFormState)
  
    const handleInputChange = event => {
      const { name, value } = event.target
  
      setUser({ ...user, [name]: value })
    }
    
    return (
    <div className ="container">
      <form
        onSubmit={event => {
          event.preventDefault()
          if (!user.name || !user.username || !user.country ) return
  
          props.addUser(user)
          setUser(initialFormState)
        }}
      >
        <div className="row">
        <div className="col">
        <label>Name</label>
        <input type="text" className ="form-control" name="name" value={user.name} onChange={handleInputChange} /></div>
        <div className="col">
        <label>Username</label>
        <input type="text"  className ="form-control" name="username" value={user.username} onChange={handleInputChange}/></div>
        <div className="col">
        <label>Country</label>
        <input type="text"  className ="form-control" name="country" value={user.country} onChange={handleInputChange} /></div>
        </div>
        <span class="input-group-btn">
        <button className="btn btn-outline-success btn-xs center" id ="form-btn">Add User</button></span>
        </form>
        </div>
    )
  }
  
  export default AddUserForm