import React, { useState, useEffect } from 'react'
import './custom.css'
import 'bootstrap/scss/bootstrap.scss'



const EditUserForm = props => {
  const [ user, setUser ] = useState(props.currentUser)

  useEffect(
    () => {
      setUser(props.currentUser)
    },
    [ props ]
  )

  const handleInputChange = event => {
    const { name, value } = event.target

    setUser({ ...user, [name]: value })
  }


  return (
    <div className = "container">
    <form
      onSubmit={event => {
        event.preventDefault()

        props.updateUser(user.id, user)
      }}
    >
    <div className="row">
        <div className="col">
      <label>Name</label>
      <input type="text" className ="form-control" name="name" value={user.name} onChange={handleInputChange} /></div>
      <div className="col">
      <label>Username</label>
      <input type="text"  className ="form-control" name="username" value={user.username} onChange={handleInputChange} /></div>
      <div className="col">
      <label>Country</label>
      <input type="text" className ="form-control" name="country" value={user.country} onChange={handleInputChange} /></div>
      
      <button className= "btn btn-light">Update user</button>
      <button onClick={() => props.setEditing(false)} className="btn btn-secondary">
        Cancel
      </button>
    </div>
    </form>
    </div>
  )
}

export default EditUserForm