import React, { useState, Fragment } from 'react'
import EditUserForm from './EditUserForm'
import AddUserForm from './AddUserForm'
import UserTable from './tables/UserTable'
import Fade from 'react-reveal/Fade'
import './custom.css'
import 'bootstrap/scss/bootstrap.scss'








const App = () => {
  
//DUMMY DATA
  const usersData = [
    { id: 1, name: 'Liza', username: 'beatudown' , country: 'Canada'},
    { id: 2, name: 'Darren', username: 'kickhighaimlow', country: 'USA' },
    { id: 3, name: 'Keiko', username: 'thousandfists'  , country: 'Japan'},
    { id: 4, name: 'Cameron', username: 'norest4weary', country: 'UK' },
  ]

  const initialFormState = { id: null, name: '', username: '', country: ''}

  //STATES
  const [users, setUsers] = useState(usersData)
  const [editing, setEditing] = useState(false)
  const [currentUser, setCurrentUser] = useState(initialFormState)

  
  const addUser = user => {
    user.id = users.length + 1
    setUsers([...users, user])
  }

  const deleteUser = id => {
    setUsers(users.filter(user => user.id !== id))
  }

  const editRow = user => {
    setEditing(true)
  
    setCurrentUser({ id: user.id, name: user.name, username: user.username, country: user.country})
  }

  const updateUser = (id, updatedUser) => {
    setEditing(false)
  
    setUsers(users.map(user => (user.id === id ? updatedUser : user)))
  }
  
  return (
    <Fade>
    <div className="top"></div>
    <div className="container">
      <h1 className = "title text-center">React w/Hooks CRUD App</h1>
      <div className="flex-row">
				<div className="flex-large">
					{editing ? (
						<Fragment>
							<h2 className ="tabletitle">Edit user</h2>
							<EditUserForm
								editing={editing}
								setEditing={setEditing}
								currentUser={currentUser}
								updateUser={updateUser}
							/>
						</Fragment>
					) : (
						<Fragment>
							<h2 className ="tabletitle">Add User</h2>
							<AddUserForm addUser={addUser} />
						</Fragment>
					)}
				</div>
				<div className="flex-large vw">
					<h2 className ="tabletitle">View Users</h2>
					<UserTable users={users} editRow={editRow} deleteUser={deleteUser} />
				</div>
			</div>
    </div>
  </Fade>
  )
}

export default App
